/* stylelint-disable */
.button {
	position: relative;
	z-index: 0;
	display: inline-block;
	padding: 16px 53px;
	font-family: $font-primary;
	font-style: normal;
	font-weight: bold;
	font-size: 16px;
	line-height: 24px;
	outline: none;
	background-color: transparent;
	color: #fff;
	border: none;
	cursor: pointer;
	text-decoration: none;
	overflow: hidden;
	transition: all 0.5s;

	&:after,
	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		display: block;
		width: 100%;
		height: 100%;
		z-index: -1;
		transform: translate3D(-101%, 0, 0);
		transition: all 0.5s;
	}

	&:before {
		z-index: -2;
		transform: translate3D(0, 0, 0);
	}

	&:hover,
	&:focus {
		&:after {
			transform: translate3D(0, 0, 0);
			transition: all 0.5s;
		}
	}

	&.button--primary {
		&:before {
			background-color: $button-primary;
		}

		&:after {
			background-color: $button-secondary; /* onhover background color */
		}
	}

	&.button--secondary {
		&:before {
			background-color: $button-tertiary;
		}

		&:after {
			background-color: $button-secondary; /* onhover background color */
		}
	}

	// Disabled
	&.disabled,
	&[disabled] {
		&:hover,
		&:focus {
		}
	}
}
/* stylelint-enable */
