.header {
	padding-top: 24px;
	padding-bottom: 24px;
	background-color: $brand-primary; // TODO: remove after finish home intro section

	@include breakpoint(no-xl) {
		padding-top: 32px;
		padding-bottom: 32px;
	}

	@include breakpoint(m-xl) {
		padding-top: 26px;
		padding-bottom: 26px;
		background-color: $brand-secondary;
	}
}

.header__row {
	position: relative;
	justify-content: space-between;
}

.header__logo-wrapper {
	position: absolute;
	display: inline-block;
	line-height: 0;

	@include breakpoint(m-xl) {
		top: -4px;
		left: 50%;
		transform: translate(-50%, 0);
	}

	@include breakpoint(no-xl) {
		left: 0;
		transform: translate(-66px, 66px) rotate(270deg);
	}
}

.header__nav {
	display: flex;
	align-items: center;
}

.basket {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #ffffff;
}

.basket__count {
	position: absolute;
}
