.container {
	width: 100%;
	margin: 0 auto;
	padding-left: 32px;
	padding-right: 32px;

	&.container--banner {
		padding-left: 0;
		padding-right: 0;
		max-width: 2560px;
	}

	&.container--slider {
		padding-left: 39px;
		padding-right: 39px;
	}

	@include breakpoint(m-l) {
		padding-left: 16px;
		padding-right: 16px;
	}
}

.row {
	display: flex;
	flex-wrap: wrap;
}
